<template>
	<div class="layout">
		<Header class="header3">
			<p class="goback" @click='prev'>
				<Icon type="ios-arrow-back" slot="prefix" />
			</p>{{title}}</Header>
		<div class="bg_box">{{slogin}}</div>
		<Content class="top">
			<div class="hm_product_num">
				<Row type="flex" :gutter="16" class="de_cont">
					<col span="12">
					<p>: <span class="cl_num">{{price}}</span></p>
					</col>
					<col span="12">
					<p>:{{service}}</p>
					</col>
				</Row>
				<p class="line"></p>
				<Row type="flex" :gutter="16" class="de_cont  bottom_p">
					<col span="12">
					<p class="cont" v-html="cont"></p>
					</col>
				</Row>
			</div> 
		</Content>
		<buyservice></buyservice>
	</div>
</template>

<script>
	import buyservice from './../public/buyservice.vue';
	export default {
		name: 'gjsbfw_detail',
		data() {
			return {
				title:'',
				slogin:'',
				src: require('../../assets/deta_img.png'),
				price: '6000',
				service: '600',
				cont: '</br>1     </br>2</br>3</br>4</br> </br>4-6</br>10    6     ',

			}
		},
		components: {
			buyservice
		},
		methods: {
			prev() {
				this.$router.go(-1)
			},
		},
		props: {}
	}
</script>

<style lang="less" scoped>
	.bg_box {
		position: fixed;
		left: 0;
		top: 0;
		width: 100%;
		height: 152px;
		padding-top: 30px;
		display: block;
		background-image: url('../../assets/ser_bg.png');
		font-size: 23px;
		color: #FFFFFF;
		text-align: center;
		line-height: 122px;
		font-weight: bolder;
	}
	
	.top {
		margin-top: 152px;
	}
	
	.hm_product_num {
		width: 100%;
		display: block;
		height: auto !important;
		min-height: 20px;
		background-color: #FFFFFF;
		overflow: hidden;
	    .de_cont {
			padding:15px 20px 10px 20px;
		}
		.cl_num{
			color: #2B2EF5;
			font-size: 26px;
		}
		span {
			display: inline-block;
			text-align: left;
			font-weight: bold;
		}
		p {
			font-size: 15px;
			color: #000000;
			line-height: 22px;
			width: 50%;
			text-align: left;
			padding: 7px;
		}
		.cont {
			font-size: 14px;
			color: #000000;
			width: 100%;
			line-height: 25px;
		}
		.line {
			width: 100%;
			background-color: #D7D7D7;
			height: 1px;
			padding: 0;
		}
	}
</style>


