<template>
	<div class="layout">
		<Header class="header2">
			<p class="goback" @click='prev'>
				<Icon type="ios-arrow-back" slot="prefix" />
			</p></Header>
		<Content class="content6 top_p">

			<div class="hm_product_num">
				<Form ref="formCustom" :model="formCustom" :rules="ruleCustom" class="big_h" :label-width="120">
					<FormItem class="my_list" style="margin-top:10px;" :label-width="0">
						<img :src="formCustom.bannerUrl" class="img_box" v-if="formCustom.bannerUrl !== null">
						<Upload style="height: 30px; width: 80px; position: absolute; right: 0; top: 0; z-index: 99;" v-model="formCustom.bannerUrl" ref="upload" :show-upload-list="false" :default-file-list="defaultList" :on-success="handleSuccess" :format="['jpg','jpeg','png']" :max-size="2048" :on-format-error="handleFormatError" :on-exceeded-size="handleMaxSize" :before-upload="handleBeforeUpload" multiple type="drag" action="http://58.87.66.77:80/v1/upload/image">
							<div style="padding: 0; font-size: 13px; color: #999">
								<Icon type="ios-cloud-upload" size="28" ></Icon>
								<p></p>
							</div>
						</Upload>
					</FormItem>
					<FormItem class="my_list" prop="name" label="">
						<input v-model="formCustom.name" placeholder="20" />
					</FormItem>

					<FormItem class="my_list" prop="price" label="">
						<input v-model="formCustom.price" placeholder="" />
					</FormItem>
					<FormItem class="my_list" prop="categoryFir" label=""> 
						<Select v-model="formCustom.categoryFir" style="width:100%; float:right; margin-top:-13px;">
							<Option  v-for="(itme,key,index) in data_categoryFir":key=key :value=itme.value :label=itme.labe>{{itme.value}}</Option>
						</Select>
						</FormItem>
					<FormItem class="my_list" prop="categorySec" label="">
						 <Select v-model="formCustom.categorySec" style="width:100%; float:right; margin-top:-13px;">
							<Option value="" label=""></Option>
							<Option value="" label=""></Option>
							<Option value="" label=""></Option>
						</Select>
					</FormItem>
					<FormItem class="my_list" prop="transactionType" label="">
						<input v-model="formCustom.transactionType" placeholder="" />
					</FormItem>
					<FormItem class="my_list" prop="applyNo" label="">
						<input v-model="formCustom.applyNo" placeholder="" />
					</FormItem>
					<Button type="primary" shape="circle" @click="handleSubmit('formCustom')"></Button>
				</Form>
			</div>
		</Content>
	</div>
</template>
<script>
	import { patentcreate,patentgetById,patentupdate } from '../../api/api';
	export default {
		name: 'zlfb',
		data() {
			return {
				formCustom: {
					bannerUrl: '',
					name: '',
					price: '',
					categorySec:'',
					categoryFir:'',
					transactionType: '',
					applyNo: '',
				},
				ruleCustom: { 
					bannerUrl: [{
						required: true,
						message: '123',
						trigger: 'blur'
					}],
					name: [{
						required: true,
						message: '123',
						trigger: 'blur'
					}],

					price: [{
						required: true,
						message: '123',
						type:'number',
						trigger: 'blur'
					}],
					categorySec: [{
						required: true,
						message: '123',
						trigger: 'change'
					}],

					categoryFir: [{
						required: true,
						message: '123',
						trigger: 'change'
					}],
					transactionType: [{
						required: true,
						message: '123',
						trigger: 'blur'
					}],

					applyNo: [{
						required: true,
						message: '132',
						trigger: 'blur'
					}],
				},

				defaultList: [],
				visible: true,
				uploadList: [], 
				fileList:[], 
				data_categoryFir: [
				{
						value: '',
						label: ''
					},
					{
						value: '',
						label: ''
					},
					{
						value: '',
						label: ''
					},
					{
						value: '',
						label: ''
					},
					{
						value: '',
						label: ''
					},
					{
						value: '',
						label: ''
					},
					{
						value: '',
						label: ''
					},
					{
						value: '',
						label: ''
					}
				]
			}
		},
		components: {

		},
		methods: {
			prev() {
				this.$router.go(-1)
			},

			onload() {
				//console.log(sessionStorage.customerId)
				if(sessionStorage.customerId == null) {
					this.$router.push({
						path: '/login'
					})
				}
			},
			getProducts() {
				let para = {  
					id:this.$route.query.id,
				};
				this.listLoading = true;
				//NProgress.start();
				patentgetById(para).then((res) => { 
					this.formCustom = res.data.result; 
					this.listLoading = false;
					//NProgress.done();
				}).catch((err) => {
					console.log(err);
				});
			},
			handleRemove(file) {
				const fileList = this.$refs.upload.fileList;
				this.$refs.upload.fileList.splice(fileList.indexOf(file), 1);
			},
			handleSuccess(res, file) {
				file.bannerUrl = 'http://58.87.66.77:80' + res.result;
				this.formCustom.bannerUrl = 'http://58.87.66.77:80' + res.result;
			},
			handleFormatError(file) {
				this.$Notice.warning({
					title: '',
					desc: '' + file.name + 'jpgpng.'
				});
			},
			handleMaxSize(file) {
				this.$Notice.warning({
					title: '',
					desc: 'File  ' + file.name + '2M.'
				});
			},
			handleBeforeUpload() {
				const check = this.uploadList.length < 1;
				if(!check) {
					this.$Notice.warning({
						title: '.'
					});
				}
				return check;
			},

			prev() {
				this.$router.go(-1)
			},
			handleSubmit(name) {
				this.$refs[name].validate((valid) => {
					if(valid) {
						let para = {
							bannerUrl: this.formCustom.bannerUrl,
							name: this.formCustom.name,
							price: this.formCustom.price,
							categoryFir: this.formCustom.categoryFir,
							categorySec: this.formCustom.categorySec,
							transactionType: this.formCustom.transactionType,
							applyNo: this.formCustom.applyNo,
							customerId: sessionStorage.customerId,
							customerMobile: sessionStorage.mobile,
						};
						patentupdate(para).then((res) => {
							console.log(res.data)
							if(res.data.code == 0) {
								this.$Message.success('!');
								this.$router.push({
									path: '/zlgl'
								})
							} else {
								this.$Message.warning(res.data.message);
							}
						});
					} else {
					    // console.log(err);
						this.$Message.error('');
					}
				})
			}
		},
		mounted() {
			this.onload();
			this.uploadList = this.$refs.upload.fileList;
			this.getProducts(); 
		},
		props: {}
	}
</script>

<style lang="less" scoped>
	.bg_box {
		position: fixed;
		left: 0;
		top: 0;
		width: 100%;
		height: 152px;
		display: block;
		background-image: url('../../assets/ser_bg.png');
		font-size: 23px;
		color: #FFFFFF;
		text-align: center;
		line-height: 152px;
		font-weight: bolder;
	}
	
	.top {
		margin-top: 152px;
	}
	
	.big_h {
		margin-top: -15px;
		margin-bottom: 30px;
	}
	
	.hm_product_num {
		width: 100%;
		display: block;
		height: auto !important;
		min-height: 20px;
		background-color: #FFFFFF;
		overflow: hidden;
		.big_h {
			margin-bottom: 30px;
			.my_list {
				border-bottom: 1px solid #E6E6E6;
				display: block;
				font-size: 16px;
				-webkit-box-pack: justify;
				-ms-flex-pack: justify;
				justify-content: space-between;
				padding-top: 10px;
				margin-top: -20px;
				line-height: 50px;
				.img_box{
					width:335px;
					height: 126px;
				}
				.left {
					align-content: flex-start;
					.input_class {
						background-color: #FFFFFF;
						border: none;
					}
					.title {
						border-radius: 0 0 6px 6px;
						bottom: 0px;
						color: #000000;
						margin: 0;
						font-size: 16px;
						float: left;
						display: inline-block;
						width: 100px;
						text-align: left;
						align-content: flex-start;
					}
				}
				input {
					border: none;
					outline: none;
					font-size: 16px;
					display: inline-block;
					width: 190px;
					text-align: left;
				}
			}
		}
		.line {
			width: 100%;
			background-color: #D7D7D7;
			margin-bottom: 20px;
			height: 1px;
			padding: 0;
		}
	}
	
	.ivu-form-item-label {
		text-align: left;
		font-size: 16px;
	}
</style>


