<template>
	<div class="layout" v-model="products">
		<Header class="header2">
			<p class="goback" @click='prev'>
				<Icon type="ios-arrow-back" slot="prefix" />
			</p>{{products.name}}</Header>
		<Content class="top_p2">
			<img :src="products.bannerUrl" class="img" />
			<div class="hm_product_num">
				<Row type="flex" :gutter="16" class="de_cont">
					<col span="12">
					<p>: <br/> {{products.price}}</p>
					</col>
					<col span="12">
					<p>: <br/> {{servicePrice}}</p>
					</col>
					<col span="12">
					<p>: <br/>{{products.areaCode}}</p>
					</col>
					<col span="12">
					<p>: <br/> {{products.id}}</p>
					</col>
				</Row>

				<p class="line"></p>
				<Row type="flex" :gutter="16" class="de_cont">
					<col span="12">
					<p>: <br/> {{products.firstNoticeNo}}</p>
					</col>
					<col span="12">
					<p>: <br/> {{products.registerNoticeNo}}</p>
					</col>

					<col span="12">
					<p>: <br/> {{products.firstNoticeTime}}</p>
					</col>
					<col span="12">
					<p>: <br/> {{products.registerNoticeTime}}</p>
					</col>
					<col span="12">
					<p>: <br/> {{products.categorySec}}</p>
					</col>
					<col span="12">
					<p>: <br/> {{products.laterAppointTime}}</p>
					</col>
					<col span="12">
					<p>: <br/> {{products.expireTime}}</p>
					</col>

					<col span="12">
					<p>: <br/> {{products.internationRegisterTime}}</p>
					</col>
					<col span="12">
					<p>: <br/> {{products.priorityTime}}</p>
					</col>

					<col span="12">
					<p>: <br/> {{products.deadlineyear}}</p>
					</col>
					<col span="12">
					<p>: <br/> {{products.publicFlag == 'Y' ? '':''}}</p>
					</col>
					<Button type="primary" shape="circle">{{products.customerMobile}}</Button>
				</Row>

				<p class="line"></p>
				<div class="de_cont2 bottom_p">
					<span class="title"></span>
					<span class="smal-title"></span>
					<span class="title2">1.</span>
					<span>
					
				</span>
					<span class="title2">2.</span>
					<span>

					</span>
					<span class="title2">3.</span>
					<span>
					5959
				</span>
					<span class="title2">4.</span>
					<span>
					//
				</span>
					<span class="title2">
						5.</span>
					<span>
					1-3,6-10
				</span>
				</div>
			</div>
		</Content>
		<buyservice v-if="products.status===0"></buyservice>
		<buyservice v-if="products.status===null"></buyservice>
		<Modal v-model="modal12" title="" @on-ok="ok" @on-cancel="cancel" v-if="products.status===0">
			<Row>
				<Col span="12">
				<Rate show-text v-model="valueText" />
				</Col>
			</Row>
		</Modal>
	</div>
</template>

<script>
	import buyservice from './../public/buyservice.vue';
	import {
		brandgetById,
	} from '../../api/api';
	export default {
		name: 'sb_details',
		data() {
			return {
				products: [],
				modal12: false,
				valueText: 0,
				servicePrice:sessionStorage.brand_fee,
			}
		},
		components: {
			buyservice
		},
		methods: {
			prev() {
				this.$router.go(-1)
			},
			//
			getProducts() {
				this.listLoading = true;
				//NProgress.start(); 
				let applyType = this.$route.query.applyType;
				let para = {
					id: this.$route.query.id,
				};
				brandgetById(para).then((res) => {
					//console.log(res);
					this.products = res.data.result; 
					this.listLoading = false;
					//NProgress.done();
				}).catch((err) => {
					//console.log(err);
				});
			},
			ok() {
				this.$Message.info('Clicked ok');
			},
			cancel() {
				this.$Message.info('Clicked cancel');
			}
		},
		props: {},
		mounted() {
			this.getProducts();
		}
	}
</script>

<style lang="less" scoped>
	.img {
		width: 100%;
		height: 200px;
		display: block;
	}
	
	.top {
		margin-top: 200px;
	}
	
	.hm_product_num {
		width: 100%;
		display: block;
		height: auto !important;
		min-height: 20px;
		background-color: #FFFFFF;
		overflow: hidden;
		.de_cont {
			padding: 20px;
		}
		span {
			display: block;
			text-align: left;
		}
		p {
			font-size: 15px;
			color: #000000;
			line-height: 22px;
			width: 50%;
			text-align: left;
			padding: 7px;
		}
		.line {
			width: 100%;
			background-color: #D7D7D7;
			height: 1px;
			padding: 0;
		}
		.de_cont2 {
			padding: 20px;
			.title {
				font-size: 28px;
				color: #000000;
				font-weight: bold;
				height: 45px;
				line-height: 45px;
				text-align: center;
			}
			.smal-title {
				font-size: 13px;
				color: #9B9B9B;
				text-align: center;
			}
			.title2 {
				font-size: 18px;
				color: #2B2EF5;
				line-height: 18px;
				font-weight: bold;
				margin-top: 25px;
				margin-bottom: 8px;
			}
			span {
				font-size: 14px;
				color: #000000;
				line-height: 20px;
			}
		}
	}
</style>


