<template>
	<div class="layout">
		<Header class="header2">
			<p class="goback" @click='prev'>
				<Icon type="ios-arrow-back" slot="prefix" />
			</p></Header>
		<Content class="content2 top_p">
			<div class="hm_product_num">
                <Phone></Phone> 
				<p class="line  top_p"></p>
				<Row :gutter="16" type="flex">
					<col span="8">
					<p v-for="(item, name, index) in gj_sever_list">
						<img :src="item.src" />
						{{item.title}}</p>
					</col> 
				</Row>
			</div>
		</Content>
		<Content class="content3">
			<div class="hm_product_num">
				<span class="title"> </span>
				<Row :gutter="16">
					<Col class="produce_list" span="12">
					<img src="../../assets/img.png" />
					</Col>
					<Col class="produce_list" span="12">
					<img src="../../assets/img.png" />
					</Col>
					<Col class="produce_list" span="12">
					<img src="../../assets/img.png" />
					</Col>
					<Col class="produce_list" span="12">
					<img src="../../assets/img.png" />
					</Col>
					<Col class="produce_list" span="12">
					<img src="../../assets/img.png" />
					</Col>
					<Col class="produce_list" span="12">
					<img src="../../assets/img.png" />
					</Col>
					<Col class="produce_list" span="12">
					<img src="../../assets/img.png" />
					</Col>
					<Col class="produce_list" span="12">
					<img src="../../assets/img.png" />
					</Col>
				</Row>

			</div>
		</Content>
	</div>
</template>

<script>
	import Phone from './../public/phone.vue'; 
	export default {
		name: 'gj_service',
		data() {
			return {
				src: require('../../assets/deta_img.png'),
				price: '6000',
				service: '600',
				cont: '</br>1     </br>2</br>3</br>4</br> </br>4-6</br>10    6     ',
				gj_sever_list: [{
						src: require('../../assets/meiguo.png'),
						title: '',
					},
					{
						src: require('../../assets/oumeng.png'),
						title: '',
					},
					{
						src: require('../../assets/deguo.png'),
						title: '',
					},
					{
						src: require('../../assets/faguo.png'),
						title: '',
					},
					{
						src: require('../../assets/xiangg.png'),
						title: '',
					},
					{
						src: require('../../assets/taiwn.png'),
						title: '',
					},
					{
						src: require('../../assets/hanguo.png'),
						title: '',
					},
					{
						src: require('../../assets/riben.png'),
						title: '',
					},
					{
						src: require('../../assets/othercon.png'),
						title: '',
					}
				]
			}
		},
		components: {
           Phone,
		},
		methods: {
			prev() {
				this.$router.go(-1)
			},
		},
		props: {}
	}
</script>

<style lang="less" scoped>
	.bg_box {
		position: fixed;
		left: 0;
		top: 0;
		width: 100%;
		height: 152px;
		display: block;
		background-image: url('../../assets/ser_bg.png');
		font-size: 23px;
		color: #FFFFFF;
		text-align: center;
		line-height: 152px;
		font-weight: bolder;
	}
	
	.top {
		margin-top: 152px;
	}
	
	.hm_product_num {
		width: 100%;
		display: block;
		height: auto !important;
		min-height: 20px;
		background-color: #FFFFFF;
		overflow: hidden;
		.title {
			font-size: 28px;
			color: #000000;
			font-weight: bold;
			height: 45px;
			line-height: 45px;
			margin: 15px 0;
			display: block;
			text-align: center;
		}
		p {
			font-size: 16px;
			color: #000000;
			text-align: center;
			width: 33%;
			margin: 20px 0;
			img {
				height: 76px;
				width: 76px;
				display: block;
				margin: 0 auto 5px auto;
			}
		}
		.produce_list {
			position: relative;
			overflow: hidden;
			height: 122px;
			margin-bottom: 15px;
			img {
				border-radius: 6px;
				width: 100%;
			}
			.title {
				border-radius: 0 0 6px 6px;
				position: absolute;
				height: 30px;
				line-height: 30px;
				bottom: 0px;
				font-size: 14px;
				z-index: 999;
				color: #FFFFFF;
				display: inline-block;
				width: 91%;
				background-color: rgba(0, 0, 0, 0.5);
			}
		}
		.line {
			width: 100%;
			background-color: #D7D7D7;
			margin-top: 30px;
			height: 1px;
			padding: 0;
		}
	}
</style>


