				.img_box {
					width: 100%;
					height: 126px;
				}
				.left {
					align-content: flex-start;
					.input_class {
						background-color: #FFFFFF;
						border: none;
					}
					.title {
						border-radius: 0 0 6px 6px;
						bottom: 0px;
						color: #000000;
						margin: 0;
						font-size: 16px;
						float: left;
						display: inline-block;
						width: 100px;
						text-align: left;
						align-content: flex-start;
					}
				}
				input {
					border: none;
					outline: none;
					font-size: 16px;
					display: inline-block;
					width: 190px;
					text-align: left;
				}
			}
		}
		.line {
			width: 100%;
			background-color: #D7D7D7;
			margin-bottom: 20px;
			height: 1px;
			padding: 0;
		}
	}
