<template>
	<div class="layout">
		<Header class="header">
		<span style="float: right;" @cl>{{site}}</span></Header>
		<Content class="content top_p bottom_p">
			<div class="hm_product_num">
				<Tabs value="name1">
					<TabPane label="" name="name1" span="8">
						<Row type="flex" :gutter="16"> 
							<col span="8">
							<p  v-for="(item, name, index) in sb_fl" @click="product_list(item.categoryFir)"><img :src=item.src />{{item.name}}</p>
							</col> 
							<col span="8">
							<p @click="sb_all"><img src="../../assets/sb_all.png"/></p>
							</col>
						</Row>
					</TabPane>
					<TabPane label="" name="name2"> 
						<Row type="flex" :gutter="16">
							<div></div>
							<col span="8">
							<p  v-for="(item, name, index) in gjsb" @click="gj_product_list(item.areaCode)"><img :src=item.src />{{item.name}}</p>
							</col>
						</Row></TabPane>
					<TabPane label="" name="name3"> 
						<Row type="flex" :gutter="16">
							<div></div>
							<col span="8">
							<p  v-for="(item, name, index) in mb" @click="mb_product_list(item.categoryFir)"><img :src=item.src />{{item.name}}</p>
							</col>
							<col span="8">
							<p @click="sb_all_mb()"><img src="../../assets/sb_all.png"/></p>
							</col>
						</Row></TabPane>
					<TabPane label="" name="name4"> 
						<Row type="flex" :gutter="16">
							<div></div>
							<col span="8">
							<p  v-for="(item, name, index) in zl_fl" @click="zl_meau(item.categoryFir,item.value)"><img :src=item.src />{{item.name}}</p>
							</col>
						</Row>
					</TabPane>
				</Tabs>
			</div>
		</Content>
		<Footer class="footer">
			<Row type="flex">
				<col span="6">
				<router-link to="/"><img src="../../assets/icon_1.png" /><br /></router-link>
				</col>
				<col span="6">
				<router-link to="product" class="active"><img src="../../assets/icon_2_hover.png" /><br /></router-link>
				</col>
				<col span="6">
				<router-link to="service"><img src="../../assets/icon_3.png" /><br /></router-link>
				</col>
				<col span="6">
				<a @click="myvip"><img src="../../assets/icon_4.png" /><br /></a>
				</col>
			</Row>
		</Footer>
	</div>
</template>

<script>
	export default {
		name: 'product_list',
		data() {
			return { 
				site:this.$route.query.site,
				sb_fl: [{
						categoryFir: '01-',
						name: '01-',
						src: require('../../assets/sb_1.png'),
					},
					{
						categoryFir: '02-',
						name: '02-',
						src: require('../../assets/sb_2.png'),
					},
					{
						categoryFir: '03-',
						name: '03-',
						src: require('../../assets/sb_3.png'),
					},
					{
						categoryFir: '04-',
						name: '04-',
						src: require('../../assets/sb_4.png'),
					},
					{
						categoryFir: '05-',
						name: '05-',
						src: require('../../assets/sb_5.png'),
					},
					{
						categoryFir: '06-',
						name: '06-',
						src: require('../../assets/sb_6.png'),
					},
					{
						categoryFir: '07-',
						name: '07-',
						src: require('../../assets/sb_7.png'),
					},
					{
						categoryFir: '08-',
						name: '08-',
						src: require('../../assets/sb_8.png'),
					}
				],
				gjsb: [
				{
						areaCode: '',
						name: '',
						src: require('../../assets/meiguo.png'),
					},{
						areaCode: '',
						name: '',
						src: require('../../assets/oumeng.png'),
					},
					{
						areaCode: '',
						name: '',
						src: require('../../assets/deguo.png'),
					},
					{
						areaCode: '',
						name: '',
						src: require('../../assets/faguo.png'),
					},
					{
						areaCode: '',
						name: '',
						src: require('../../assets/xiangg.png'),
					},
					{
						areaCode: '',
						name: '',
						src: require('../../assets/taiwn.png'),
					},
					{
						areaCode: '',
						name: '',
						src: require('../../assets/hanguo.png'),
					},
					{
						areaCode: '',
						name: '',
						src: require('../../assets/riben.png'),
					},
					{
						areaCode: '',
						name: '',
						src: require('../../assets/othercon.png'),
					}
				],
				mb: [{
						categoryFir: '01-',
						name: '01-',
						src: require('../../assets/sb_1.png'),
					},
					{
						categoryFir: '02-',
						name: '02-',
						src: require('../../assets/sb_2.png'),
					},
					{
						categoryFir: '03-',
						name: '03-',
						src: require('../../assets/sb_3.png'),
					},
					{
						categoryFir: '04-',
						name: '04-',
						src: require('../../assets/sb_4.png'),
					},
					{
						categoryFir: '05-',
						name: '05-',
						src: require('../../assets/sb_5.png'),
					},
					{
						categoryFir: '06-',
						name: '06-',
						src: require('../../assets/sb_6.png'),
					},
					{
						categoryFir: '07-',
						name: '07-',
						src: require('../../assets/sb_7.png'),
					},
					{
						categoryFir: '08-',
						name: '08-',
						src: require('../../assets/sb_8.png'),
					}
				],
				zl_fl: [
				{
						categoryFir: '',
						value:'001',
						name: '',
						src: require('../../assets/shxy.png'),
					},
					{
						categoryFir: '',
						value:'002',
						name: '',
						src: require('../../assets/zyys.png'),
					},
					{
						categoryFir: '',
						value:'003',
						name: '',
						src: require('../../assets/hxyj.png'),
					},
					{
						categoryFir: '',
						value:'004',
						name: '.',
						src: require('../../assets/fzzz.png'),
					},
					{
						categoryFir: '',
						value:'005',
						name: '',
						src: require('../../assets/gdjz.png'),
					},
					{
						categoryFir: '',
						value:'006',
						name: '',
						src: require('../../assets/jxgc.png'),
					},
					{
						categoryFir: '',
						value:'007',
						name: '',
						src: require('../../assets/wul.png'),
					},
					{
						categoryFir: '',
						value:'008',
						name: '',
						src: require('../../assets/dianx.png'),
					},
				]
			}
		},
		components: {

		},
		methods: { 
			
      open() {
        const h = this.$createElement;
        this.$msgbox({
          title: '',
          message: h('p', null, [
            h('span', null, ' '),
            h('i', { style: 'color: teal' }, 'VNode')
          ]),
          showCancelButton: true,
          confirmButtonText: '',
          cancelButtonText: '',
          beforeClose: (action, instance, done) => {
            if (action === 'confirm') {
              instance.confirmButtonLoading = true;
              instance.confirmButtonText = '...';
              setTimeout(() => {
                done();
                setTimeout(() => {
                  instance.confirmButtonLoading = false;
                }, 300);
              }, 3000);
            } else {
              done();
            }
          }
        }).then(action => {
          this.$message({
            type: 'info',
            message: 'action: ' + action
          });
        });
      },
			product_list(categoryFir) {
				if (categoryFir !='all') {
				this.$router.push({
					path: '/sb_list', 
                    query:{  
                    categoryFir:categoryFir, // orderNum : this.searchData.orderNo
                    applyType:'1',
                    inlandFlag:'Y',
                    areaCode:this.$route.query.site,
                    } 
				}) 
				} else{
					this.$router.push({
					path: '/sb_all',  
				}) 
				}
			}, 
			
			mb_product_list(categoryFir) {
				if (categoryFir !='all') {
				this.$router.push({
					path: '/sb_list', 
                    query:{  
                    categoryFir:categoryFir,
                    vipFlag:'Y',
                    applyType:'1',
                    areaCode:this.$route.query.site,
                    } 
				}) 
				} else{
					this.$router.push({
					path: '/sb_all',  
                    query:{   
                    vipFlag:'Y',
                    areaCode:this.$route.query.site,
                    } 
				}) 
				}
			}, 
			
			gj_product_list(categoryFir) { 
				this.$router.push({
					path: '/sb_list', 
                    query:{  
                    categoryFir:categoryFir, // orderNum : this.searchData.orderNo
                    applyType:'1',
                    inlandFlag:'N',
                    areaCode:this.$route.query.site,
                    } 
				})  
			}, 
			zl_meau(categoryFir,value) {
				this.$router.push({
					path: '/zl_meau', 
                    query:{  
                    	categoryFir:categoryFir, 
                    applyType:'2',
                    value:value,
                    areaCode:this.$route.query.site,
                    } 
				})
			},
			
			sb_all() {
				this.$router.push({
					path: '/sb_all'
				})
			},
			sb_all_mb() {
				this.$router.push({
					path: '/sb_all',
					query:{ 
                    inlandFlag:'Y',
                    areaCode:this.$route.query.site,
                    } 
				})
			},
			myvip() {
				let loginName = sessionStorage.loginName
				if(loginName != null) {
					this.$router.push({
						path: '/my'
					})
				} else {
					this.$router.push({
						path: '/login'
					})
				}
			},
		},
		props: {}
	}
</script>

<style lang="less" scoped>
	.hm_product_num {
		width: 100%;
		display: block;
		height: auto !important;
		min-height: 20px;
		overflow: hidden;
		p {
			font-size: 14px;
			color: #000000;
			text-align: center;
			width: 33%;
			margin: 16px 0 26px 0;
			img {
				height: 76px;
				width: 76px;
				display: block;
				margin: 0 auto 5px auto;
			}
		}
	}
</style>


